/**
 * Area affichant une propriété d'un WSP
 */
export abstract class WspArea<C extends IControlLabelContext = IControlLabelContext> extends LabelArea<IControlLabelContext & IRegPointer<IWspUiEnv>> {

	isVisible(ctx: IControlLabelContext & IRegPointer<IWspUiEnv>): boolean {
		if (!ctx.reg.env.wsp) return false;
		return super.isVisible(ctx);
	}
}

/**
 * Code de l'atelier
 */
class WspCodeInputArea extends StringInputArea<IRegPointer<IWspUiEnv>> {

	constructor() {
		super("code", "text");
		this.setLabel("Code");
		this.setPattern("[a-zA-Z0-9_]+");
		this.setRequired(true);
		this.setInputCustomValidity(async function (this: HTMLInputElement, ctx: IRegPointer<IWspUiEnv>): Promise<string> {
			if (!this.checkValidity()) return "Seuls les caractères alpha-numériques et _ sont autorisés";
			if (this.value) {
				const code = this.value;
				let infoWsp = await ctx.reg.env.universe.wspServer.config.adminWspUrl.fetchJson<JInfoWsp>(IO.qs("cdaction", "InfoWsp", "param", code));
				if (infoWsp && infoWsp.status != 'noWsp') {
					return `L'atelier avec le code '${code}' existe déjà.`;
				}
			}
			return "";
		}, 500, true)
	}

	_buildControl(ctx: IRegPointer<IWspUiEnv>, name: string): HTMLInputElement {
		let input = super._buildControl(ctx, name);
		DOM.setAttr(input, "spellcheck", "false");
		DOM.setAttr(input, "autocomplete", "current-password");// "off" ne fonctionne pas
		return input;
	}

}

