export class WspStatus extends BaseElementAsync implements IFormJsonisable {
	protected reg: IReg<IWspUiEnv>;
	protected labelElt: MsgLabel;
	protected migrlBtn: ActionBtn<IRegPointer<IWspUiEnv>>;

	protected async _initialize(init: OSkinableInit & IRegPointer<IWspUiEnv>): Promise<any> {
		const sr = this.attachShadow(DOMSH.SHADOWDOM_INIT);
		this._initAndInstallSkin(this.localName, init);
		this.reg = REG.findReg(this, init);
		this.labelElt = sr.appendChild(<MsgLabel class="status" î={{label: "Chargement de l'atelier en cours..."} as OLabelInit}/>) as MsgLabel;
		sr.appendChild(<div class="spacer"/>);
		this.migrlBtn = sr.appendChild(<ActionBtn
			î={{reg: init.reg, actionContext: init, label: "Migrer", action: new AutoMigrWspAction(), uiContext: "custom"} as OActionBtnInit<IRegPointer<IWspUiEnv>>}/>) as ActionBtn<IRegPointer<IWspUiEnv>>;
		await this._refreshAll();
	}

	protected async _refreshAll(): Promise<void> {
		let infoWsp = this.reg.env.wsp.infoWsp || this.reg.env.wsp.infoWspError;
		if (this.reg.env.wsp.migrating) {
			this.labelElt.setCustomMsg("Migration de l'atelier en cours...", "info");
		}else if (infoWsp?.isMigrationNeeded) {
			this.labelElt.setCustomMsg("Migration des contenus nécessaire", "warning");
		} else if (this.reg.env.wsp.isInError === true) {
			if (this.reg.env.wsp.infoWsp && !this.reg.env.wsp.wspMetaUi)
				this.labelElt.setCustomMsg("Échec au chargement du modèle documentaire", "error");
			else
				this.labelElt.setCustomMsg(this.reg.env.wsp.infoWspError && this.reg.env.wsp.infoWspError.status === "noWsp" ? "Atelier non trouvé" : "Atelier en erreur", "error");
		} else if (this.reg.env.wsp.isDeleted === true) {
			this.labelElt.setCustomMsg("Atelier supprimé", "error");
		} else if (this.reg.env.wsp.isAccessDenied === true) {
			this.labelElt.setCustomMsg("Accès non autorisé", "error");
		} else if (this.reg.env.wsp.wspMetaUi === null) {
			this.labelElt.setCustomMsg("Accès impossible aux propriétés de l'atelier", "error");
		} else if (this.reg.env.wsp.fullLoading) {
			this.labelElt.setCustomMsg("Chargement de l'atelier en cours...", "info");
		} else {
			this.labelElt.setCustomMsg("Opérationnel");
		}
		this.migrlBtn.refresh();
	}

	extractJson(parent: Dict<any>): boolean {
		this.reg.env.wsp.waitForLoad().then(() => {
			this._refreshAll();
		});
		return false;
	}

	fillJson(parent: any, root: any): void {
		//NA
	}
}

