class WspDrfRefWspTitleArea extends LabelAsyncArea<IRegPointer<IWspUiEnv> & IControlLabelContext> {

	constructor() {
		super("wspMaster");
		this.setLabel("");
	}

	async _loadControl(ctx: IRegPointer<IWspUiEnv> & IControlLabelContext, name: string): Promise<HTMLElement> {
		let infoWsp = ctx.reg.env.wsp.infoWsp || ctx.reg.env.wsp.infoWspError;
		let str;
		if (infoWsp) {
			let masterWspCd = infoWsp.props.drfRefWsp;
			let masterWsp = new Wsp(ctx.reg.env.wsp.wspServer, masterWspCd);
			await masterWsp.waitForLoad();
			const wspTitle = masterWsp.wspTitle;
			str = `Calque de travail de '${wspTitle}'`;
		} else
			str = 'Calque de travail';
		return <div><span class="important">{str}</span></div>;
	}

}

