/**
 * [DRV] Sources de dérivation
 */
class WspDrvDefaultSrcFindPathInputArea<C extends IRegPointer<IWspUiEnv> & IControlAreaContext> extends ControlAsyncArea<C> {
	constructor() {
		super("drvDefaultSrcFindPath");
		this._label = "Sources de dérivation";
		this._description = "Sources intermédiaires de dérivation (optionnel)";
	}

	isEnabled(ctx: C): boolean {
		let infoWsp = ctx.reg.env.wsp.infoWsp || ctx.reg.env.wsp.infoWspError;
		if (!infoWsp) return false;
		return super.isEnabled(ctx);
	}

	async _loadControl(ctx: C, name: string): Promise<InputOrderedSetPanel> {
		// Liste des valeurs
		let infoWsp = ctx.reg.env.wsp.infoWsp || ctx.reg.env.wsp.infoWspError;
		let dataSet: Dataset<DatasetEntry<IWspUiEnv & IControlAreaContext>> = {};
		if (infoWsp) {
			if (WSP_DRV.isDrv(infoWsp.props)) {
				let currentDrvAxis = infoWsp.props.drvAxis;
				let masterWspCd = infoWsp.props.drvMasterWsp;
				let masterWsp = new Wsp(ctx.reg.env.wsp.wspServer, masterWspCd);
				await masterWsp.waitForLoad();
				if (masterWsp.infoWsp)
					masterWsp.infoWsp.props.listDrvs.split(" ").forEach((drvAxis: string) => {
						// FIXME : récupérer le titre des ateliers
						if (drvAxis !== currentDrvAxis) dataSet[drvAxis] = {label: drvAxis};
					});
			} else {
				// Cas 2 : l'atelier courant est un atelier master
				// Refresh utile de l'infoWsp car ces propriétés ne sont pas dynamiquement rafraichies
				let infoWsp = await ctx.reg.env.wsp.wspServer.config.adminWspUrl.fetchJson<JInfoWsp>(IO.qs("cdaction", "InfoWsp", "param", ctx.reg.env.wsp.code, "wspPropsOpts", "(drvWspUsedBy 'true' listDrvs 'true' listDrfs 'true')"));
				if (infoWsp.props.listDrvs)
					infoWsp.props.listDrvs.split(" ").forEach((drvCode: string) => {
						// FIXME : récupérer le titre des ateliers
						dataSet[drvCode] = {label: drvCode};
					});
			}
		}
		// Input
		const inputElt = new InputOrderedSetPanel().initialize({dataSet, selectionLabel: "Sources sélectionnées", availableLabel: "Sources disponibles", emptySelectionMsg: "Aucune", name});
		inputElt.title = this.getDescription(ctx);
		const defaultValue = this.getDefaultValue(ctx);
		if (defaultValue != null) inputElt.value = defaultValue;
		return inputElt;
	}

}

/**
 * [DRV] axis
 * Contexte : atelier master
 */
class WspDrvAxisInputArea extends StringInputArea<IRegPointer<IWspUiEnv>> {
	constructor() {
		super("drvAxis", "text");
		this.setLabel("Code de dérivation");
		this.setPattern("[a-zA-Z0-9\-_]+");
		this.setInputCustomValidity(async function (this: HTMLInputElement, ctx: IRegPointer<IWspUiEnv>): Promise<string> {
			// Refresh utile de l'infoWsp car ces propriétés de sont pas dynamiquement rafraichies
			let infoWsp = await ctx.reg.env.wsp.wspServer.config.adminWspUrl.fetchJson<JInfoWsp>(IO.qs("cdaction", "InfoWsp", "param", ctx.reg.env.wsp.code, "wspPropsOpts", "(drvWspUsedBy 'true' listDrvs 'true' listDrfs 'true')"));
			if (!this.checkValidity()) return "Seuls les caractères alpha-numériques, tiret et underscore sont autorisés";
			const code = this.value;
			if (infoWsp && infoWsp.props.listDrvs && infoWsp.props.listDrvs.split(" ").indexOf(this.value) > -1) return `Le code de dérivation '${code}' a déjà été utilisé.`;
			return "";
		}, 500)
	}
}


