/**
 * [db] options
 */
class WspOptionsInputArea extends ControlArea<IControlLabelContext & IRegPointer<IChainEnv>> {
	constructor(protected noPublicWsp=false, protected noExtIt=false, protected noAirIt=false) {
		super("wspOptions");
		this.setLabel("Options");
		this.setReadOnly(updatePropReadonly)
	}

	_buildControl(ctx: IControlLabelContext & IRegPointer<IChainEnv|IWspUiEnv>, name: string): HTMLDivElement {
		let rootElt: HTMLDivElement = <div/> as HTMLDivElement;

		let isInDrfWspCtx : true|undefined = undefined;
		let isInDrvWspCtx : true|undefined = undefined;
		if((ctx as IRegPointer<IWspUiEnv>).reg.env.wsp){
			const wspDefProps = (ctx as IRegPointer<IWspUiEnv>).reg.env.wsp.wspDefProps;
			if(wspDefProps?.drfRefWsp) isInDrfWspCtx = true;
			if(wspDefProps?.drvAxis) isInDrvWspCtx = true;
		}

		function fAssignFormInput(input: HTMLInputElement) {
			Object.assign(input, {
				fillJson: function (parent: Jsonisable, root: Jsonisable) {
					parent[input.name] = input.checked ? true : false;
				},
				extractJson: function (parent: Dict<any>) {
					const value = parent[input.name];
					input.checked = value == true ? true : false;
					if (input.parentElement.classList.contains("deprecated"))
						DOM.setHidden(input.parentElement, !input.checked);
					return true;
				}
			} as IFormJsonisable);
		}

		// publicWsp
		const publicWsp = ctx.reg.env.universe.getWspFeatureStatus("publicWsp");
		if (!this.noPublicWsp && publicWsp && publicWsp != "alwaysFalse") {
			let id = this.getId() + "~publicWsp";
			const inputElt = <input name="publicWsp" id={id} type="checkbox" disabled={this.isReadOnly(ctx) || publicWsp.startsWith('always') ? true : null}
															checked={publicWsp.toLocaleLowerCase().indexOf("True") >= 0 ? true : null}/> as HTMLInputElement;
			rootElt.appendChild(<div class={`optionslist ${publicWsp == 'deprecated' ? 'deprecated' : ''}`}
															 title="Passe cet atelier dans un statut 'public'. Tout utilisateur connecté dispose d'un accès en lecture à un atelier dit 'public'. Cette option doit être activée pour permettre à d'autres ateliers d'y référencer des items étrangers (liens directs).">
				{inputElt}
				<label for={id}>Atelier public</label>
				{publicWsp === 'deprecated' ? <span class="titleDeprecated">fonction dépréciée</span> : ''}
			</div>);
			fAssignFormInput(inputElt);
		}
		// extIt
		const extIt = ctx.reg.env.universe.getWspFeatureStatus("extIt");
		if (!this.noExtIt && !isInDrfWspCtx && !isInDrvWspCtx && extIt != "alwaysFalse") {
			let id = this.getId() + "~extIt";
			const inputElt = <input name="extIt" id={id} type="checkbox" disabled={this.isReadOnly(ctx) || extIt.startsWith('always') ? true : null} checked={extIt.toLocaleLowerCase().indexOf("True") >= 0 ? true : null}/> as HTMLInputElement;
			rootElt.appendChild(<div class={`optionslist ${extIt == 'deprecated' ? 'deprecated' : ''}`}
															 title="Active les fonctions de liens inter-ateliers directs (items étrangers). Cela permet aux items de cet atelier de référencer des items d'un autre atelier 'public'.">
				{inputElt}
				<label for={id}>Liens inter-ateliers directs</label>
				{extIt === 'deprecated' ? <span class="titleDeprecated">fonction dépréciée</span> : ''}
			</div>);
			fAssignFormInput(inputElt);
		}
		// airIt
		const airIt = ctx.reg.env.universe.getWspFeatureStatus("airIt");
		if (!this.noAirIt && !isInDrfWspCtx && !isInDrvWspCtx && airIt && airIt != "alwaysFalse") {
			let id = this.getId() + "~airIt";
			const inputElt = <input name="airIt" id={id} type="checkbox" disabled={this.isReadOnly(ctx) || airIt.startsWith('always') ? true : null} checked={airIt.toLocaleLowerCase().indexOf("True") >= 0 ? true : null}/> as HTMLInputElement;
			rootElt.appendChild(<div class={`optionslist ${airIt == 'deprecated' ? 'deprecated' : ''}`}
															 title="Active les fonctions liées aux items 'flottants' (non ancrés dans un espace).">
				{inputElt}
				<label for={id}>Items 'flottants'</label>
				{airIt === 'deprecated' ? <span class="titleDeprecated">fonction dépréciée</span> : ''}
			</div>);
			fAssignFormInput(inputElt);
		}

		return rootElt;
	}

	isVisible(ctx: IControlLabelContext & IRegPointer<IChainEnv>): boolean {
		if ((!ctx.reg.env.universe.getWspFeatureStatus("airIt") || ctx.reg.env.universe.getWspFeatureStatus("airIt") == "alwaysFalse")
			&& (!ctx.reg.env.universe.getWspFeatureStatus("extIt") || ctx.reg.env.universe.getWspFeatureStatus("extIt") == "alwaysFalse")
			&& (!ctx.reg.env.universe.getWspFeatureStatus("airIt") || ctx.reg.env.universe.getWspFeatureStatus("airIt") == "alwaysFalse")
		)
			return false;
		return super.isVisible(ctx);
	}
}

