type WspTypeInstNewContext = IControlLabelContext & IRegPointer<IChainEnv> & (IRegPointer<IWspUiEnv> | IRegsPointer<IWspUiEnv>);

/**
 * WspTypeInst : création
 */
class WspTypeInstNewInputArea extends ControlAsyncArea<WspTypeInstNewContext> {
	constructor() {
		super("wspType");
		this.setLabel("Modèle");
		this.setRequired(true);
	}

	async _loadControl(ctx: WspTypeInstNewContext): Promise<HTMLElement> {
		let fromWsp = ctx.reg && 'wsp' in ctx.reg.env && ctx.reg.env.wsp;
		let wspTypeSelector = new WspTypeSelector().initialize({
			mode: "new",
			reg: ctx.reg,
			fromWspCode: fromWsp ? fromWsp.code : null,
			name: this._id,
			disabled: !this.isEnabled(ctx),
			required: this.isRequired(ctx)
		});
		wspTypeSelector.className = "formElt";
		return wspTypeSelector.initializedAsync;
	}
}

type WspTypeInstEditContext = IControlLabelContext & IRegPointer<IWspUiEnv>;

/**
 * WspTypeInst : édition
 */
class WspTypeInstEditArea extends ControlAsyncArea<WspTypeInstEditContext> {
	constructor() {
		super("wspType");
		this.setLabel("Modèle");
	}

	async _loadControl(ctx: WspTypeInstEditContext, name: string): Promise<HTMLElement> {
		let wspTypeSelector = new WspTypeSelector().initialize({
			mode: "edit",
			reg: ctx.reg,
			fromWspCode: ctx.reg.env.wsp?.code,
			name: this._id,
			disabled: !this.isEnabled(ctx),
			required: this.isRequired(ctx),
			readonly: this.isReadOnly(ctx)
		});
		wspTypeSelector.className = "formElt";
		return wspTypeSelector.initializedAsync;
	}

	isReadOnly(ctx: WspTypeInstEditContext): boolean {
		return !ctx.reg.hasPerm("action.wspProps#update.wsptype");
	}
}


/**
 * admin wsp
 */
class AdminWspToolbarArea extends LabelArea {
	constructor(id: string) {
		super(id);
		this.setLabel("Administration de l'atelier");
		this.requireVisiblePerm("action.wspProps#admin.wsp");
	}

	_buildControl(ctx: IControlLabelContext & IRegPointer<IWspUiEnv>, name: string): HTMLElement {
		return <c-bar-actions î={{
			reg: REG.getReg(ctx),
			actions: REG.getReg(ctx).getList<Action<IRegPointer<IWspUiEnv>>>(`actions:wspProps:${this.getId()}`),
			actionContext: {
				reg: REG.getReg(ctx),
			} as IRegPointer<IWspUiEnv>,
			disableFullOverlay: true,
			uiContext: "dialog",
		} as OBarActionsInit<IRegPointer<IWspUiEnv>>} id="actions" hidden="true"/>;
	}

}

//c-action

/**
 * Skinset
 * Contexte : Wsp
 */
export class SkinsetEditInputArea extends ControlAsyncArea<IControlLabelContext & IRegPointer<IWspUiEnv>> {

	constructor() {
		super("skins");
		this.setLabel("Habillages graphiques");
		this.setDescription("Habillages graphiques des générateurs du modèle documentaire");
	}

	async loadBody(ctx: IControlLabelContext & IRegPointer<IWspUiEnv>, lastDatas?: JLastDatas): Promise<HTMLElement> {
		let body = await super.loadBody(ctx, lastDatas);
		if (!await WSP.checkWspSvc(ctx.reg.env.wsp, body, "skinSet"))
			DOM.setHidden(body, true)
		return body;
	}

	async _loadControl(ctx: IControlLabelContext & IRegPointer<IWspUiEnv>, name: string): Promise<HTMLElement> {
		const skinsetSelector = new SkinSetSelector().initialize({
			reg: ctx.reg,
			wsp: ctx.reg.env.wsp,
			propsJsonName: this._id,
			disabled: !this.isEnabled(ctx),
		});
		return skinsetSelector.initializedAsync;
	}

	isEnabled(ctx: IControlLabelContext & IRegPointer<IWspUiEnv>): boolean {
		return ctx.reg.hasPerm("action.wspProps#update.wsptype");
	}
}

function updatePropReadonly(ctx: IRegPointer<IWspUiEnv>) {
	return !ctx.reg.hasPerm("action.wspProps#update.props");
}

function fromWspPropReadonly(ctx: IRegPointer<IWspUiEnv>) {
	return !ctx.reg.env.wsp.isLoaded;
}


